@mixin leaderboard($number, $shadow, $c1, $c2, $c3, $c4, $c5, $c6, $c7, $c8, $c9, $c10) {
  width: 450px;
  background: linear-gradient(to bottom, #3a404d, #181c26);
  border-radius: 10px;
  box-shadow: 0 7px 30px rgba(62, 9, 11, .3);

  h1 {
    font-size: 22px;
    color: #e1e1e1;
    padding: 12px 13px 18px;
  }

  ol {
    // counter-reset: leaderboard;
    list-style: none;
    padding-left: 0;  

    li {
      position: relative;
      z-index: 1;
      font-size: 14px;
      counter-increment: leaderboard;
      padding: 18px 10px 18px 50px;
      cursor: pointer;
      backface-visibility: hidden;
      transform: translateZ(0) scale(1.0, 1.0);

      &::before {
        content: counter(leaderboard);
        position: absolute;
        z-index: 2;
        top: 19px;
        left: 15px;
        width: 20px;
        height: 20px;
        line-height: 20px;
        color: #1c2022;
        background: $number;
        border-radius: 20px;
        text-align: center;
      }

      mark {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 18px 10px 18px 50px;
        margin: 0;
        background: none;
        color: #111518;
        font-size: 110%;

        &::before, &::after {
          content: '';
          position: absolute;
          z-index: 1;
          bottom: -11px;
          left: -9px;
          border-top: 10px solid #008080;
          border-left: 10px solid transparent;
          transition: all .1s ease-in-out;
          opacity: 0;
        }

        &::after {
          left: auto;
          right: -9px;
          border-left: none;
          border-right: 10px solid transparent;
        }
      }

      small {
        position: relative;
        z-index: 2;
        display: block;
        text-align: right;
        color: #111518;
        font-size: 110%;
      }

      &::after {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $shadow;
        box-shadow: 0 3px 0 rgba(0, 0, 0, .2);
        // transform: scaleX(1.06) scaleY(1.03);
        transition: all .3s ease-in-out;
        opacity: 0;
      }

      &:nth-child(1) {
        background: $c1;
        &::after {
          background: $c1;
          box-shadow: 0 2.5px 0 rgba(0, 0, 0, .10);
        }

        & mark {
          &::before, &::after {
            border-top: 10px solid $shadow;
            bottom: -11px;
          }
        }
      }

      &:nth-child(2) {
        background: $c2;
        &::after {
          background: $c2;
          box-shadow: 0 2px 0 rgba(0, 0, 0, .08);
        }

        & mark {
          &::before, &::after {
            border-top: 8px solid $shadow;
            bottom: -9px;
          }
        }
      }

      &:nth-child(3) {
        background: $c3;
        &::after {
          background: $c3;
          box-shadow: 0 1.5px 0 rgba(0, 0, 0, .06);
        }

        & mark {
          &::before, &::after {
            border-top: 6px solid $shadow;
            bottom: -7px;
          }
        }
      }

      &:nth-child(4) {
        background: $c4;
        &::after {
          background: $c4;
          box-shadow: 0 1px 0 rgba(0, 0, 0, .04);
        }

        & mark {
          &::before, &::after {
            border-top: 4px solid $shadow;
            bottom: -5px;
          }
        }
      }

      &:nth-child(5) {
        background: $c5;
        &::after {
          background: $c5;
          box-shadow: 0 0.5px 0 rgba(0, 0, 0, .02);
        }

        & mark {
          &::before, &::after {
            border-top: 2px solid $shadow;
            bottom: -3px;
          }
        }
      }

      &:nth-child(6) {
        background: $c6;
        &::after {
          background: $c6;
          box-shadow: 0 -0.5px 0 rgba(0, 0, 0, .02);
        }

        & mark {
          &::before, &::after {            
            top: -2px;
            bottom: auto;
            border-top: none;
            border-bottom: 2px solid $shadow;
          }
        }
      }

      &:nth-child(7) {
        background: $c7;
        &::after {
          background: $c7;
          box-shadow: 0 -1px 0 rgba(0, 0, 0, .04);
        }

        & mark {
          &::before, &::after {            
            top: -4px;
            bottom: auto;
            border-top: none;
            border-bottom: 4px solid $shadow;
          }
        }
      }

      &:nth-child(8) {
        background: $c8;
        &::after {
          background: $c8;
          box-shadow: 0 -1.5px 0 rgba(0, 0, 0, .06);
        }

        & mark {
          &::before, &::after {
            top: -6px;
            bottom: auto;
            border-top: none;
            border-bottom: 6px solid $shadow;
          }
        }
      }

      &:nth-child(9) {
        background: $c9;
        &::after {
          background: $c9;
          box-shadow: 0 -2px 0 rgba(0, 0, 0, .08);
        }

        & mark {
          &::before, &::after {
            top: -8px;
            bottom: auto;
            border-top: none;
            border-bottom: 8px solid $shadow;;
          }
        }
      }

      &:nth-child(10) {
        background: $c10;
        border-radius: 0 0 10px 10px;
        &::after {
          background: $c10;
          box-shadow: 0 -2.5px 0 rgba(0, 0, 0, .10);
          border-radius: 0 0 10px 10px;
        }

        & mark {
          &::before, &::after {
            top: -10px;
            bottom: auto;
            border-top: none;
            border-bottom: 10px solid $shadow;
          }
        }
      }

    }

    // hover
    li:hover {
      z-index: 2;
      overflow: visible;

      &::after {
        opacity: 1;
        transform: scaleX(1.06) scaleY(1.03);
      }

      & mark {
        &::before, &::after {
          opacity: 1;
          transition: all .35s ease-in-out;
        }
      }
    }

  }
}

.leaderboard-Employee {
  @include leaderboard( #cff2f1, #018c98, 
  #1cd9d7, #19d1d0,  #16c9ca,  #13c1c4, #11babd, 
  #0eb2b7, #0baab1, #09a3aa, #069ba4, #03939e)
}

.leaderboard-Manager {
  @include leaderboard( #cefdd0, #08b209, 
  #71f47a, #66ed6e,  #5ce763,  #51e058, #47da4d, 
  #3dd342, #32cd36, #28c62b, #1dc020, #13b915)
}

.leaderboard-Self {
@include leaderboard( #FADBD8, #B03A2E, 
#F1948A, #F1948A, #F1948A, #F1948A, #F1948A, 
#F1948A, #F1948A, #F1948A, #F1948A, #F1948A)
}
